<!-- 发布求购 -->
<template>

	<view class="content dis_flex ju_b">
		<view class="fq_left">
			<view :class="{'fq_left_itemA':item.active}" class="fs12 fw400 fq_left_item dis_flex ju_c aic"
				v-for="(item,index) in navList" :key="index" @click="tab(index)">
				{{item.text}}
			</view>
		</view>
		<view class="fq_right">
			<view class="fqrt dis_flex ju_c aic">
				<text style="color: #eee;">———</text>
				<text class="fs12 fw400" style="margin: 0 0.25rem;"> 热门推荐 </text>
				<text style="color: #eee;">———</text>
			</view>
			<view class="fqrbody dis_flex">
				<view class="hsl_item dis_flex_c ju_c aic" v-for="(item,index) in hzList" :key="index" @click="jump" :data-url="fabu2Url">
					<image :src="getimg(item.img)" mode="aspectFill"></image>
					<text class="hsl_text">{{item.text}}</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				fabu2Url:'/pagesA_lzc/fabu2/fabu2',
				navList: [{
						text: '热门推荐',
						active: true
					},
					{
						text: '普通公司',
						active: false
					},
					{
						text: '商标转让',
						active: false
					},
					{
						text: '互联网类',
						active: false
					},
					{
						text: '建筑资质',
						active: false
					},
					{
						text: '商标转让',
						active: false
					}

				],
				hzList: [{
						img: '/static/images/lzc/index/qiye.png',
						text: '企业注册'
					},
					{
						img: '/static/images/lzc/index/shipin.png',
						text: '食品许可'
					},
					{
						img: '/static/images/lzc/index/r.png',
						text: '商标注册'
					},
					{
						img: '/static/images/lzc/index/zhishi.png',
						text: '高企认证'
					},
					{
						img: '/static/images/lzc/index/renzheng.png',
						text: '双软认证'
					}
				],
			}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			tab(index){
				for (var i = 0; i < this.navList.length; i++) {
					if(i == index){
						this.navList[i].active = true;
					}else{
						this.navList[i].active = false;
					}
				}
			}
		}
	}
</script>

<style>
	page {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.fq_left {
		width: 209upx;
		height: 100vh;
		background-color: #f8f8f8;
		min-width: 209upx;
	}

	.fq_right {
		width: 100%;
		height: 100vh;
		min-width: 540upx;
		background-color: #fff;
	}

	.fq_left_item {
		width: 100%;
		height: 98upx;
		color: #333;
	}

	.fq_left_itemA {
		background-color: #fff;
	}

	.fqrt {
		width: 100%;
		height: 98upx;
		color: #333;
	}

	.fqrbody {
		flex-wrap: wrap;
	}

	.hsl_item {
		width: 33.3%;
		min-width: 33.3%;
		padding: 32upx 0;

	}

	.hsl_item image {
		height: 41upx;
		width: 40upx;
	}

	.hsl_text {
		font-size: 22upx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #333333;
		margin-top: 20upx;
	}
</style>
